<template>
  <v-ons-page>
    <v-ons-toolbar class="home-toolbar">
      <div class="left">
        <v-ons-toolbar-button @click="$store.commit('splitter/toggle')">
          <v-ons-icon icon="fa-bars"></v-ons-icon>
        </v-ons-toolbar-button>
      </div>
      <div class="center">{{ msg }}</div>
      <div v-if="allowInstall" class="right">
        <v-ons-toolbar-button id="installbutton" @click="install()">
          +
        </v-ons-toolbar-button>
      </div>
    </v-ons-toolbar>

    <v-ons-list>
      <v-ons-list-item v-for="item in words" :key="item.link">
        <div class="left">{{ item.nb }}</div>
        <div class="right">{{ item.az }}</div>
      </v-ons-list-item>
    </v-ons-list>

  </v-ons-page>
</template>

<script>
export default {
  name: 'home',
  computed: {
  },
  data () {
    return {
      msg: 'Ordliste barnehjemmet',
      allowInstall: true,
      words: [
        {az: 'Aserbajdsjansk', nb: 'Norsk'},
        {az: 'Diş fırçası', nb: 'tannkrem'},
        {az: 'sabun', nb: 'såpe'},
        {az: 'Diş pastası', nb: 'tannkrem'},
        {az: 'desmal', nb: 'håndduk'},
        {az: 'pampers', nb: 'bleie'},
        {az: 'paltar', nb: 'klesplagg'},
        {az: 'kalyeska', nb: 'rullestol'},
        {az: 'Yemək', nb: 'mat/å spise'},
        {az: 'Yaş salfetka', nb: 'våtserviett'},
        {az: 'Qaşıq', nb: 'skje'},
        {az: 'gəl', nb: 'kom'},
        {az: 'get', nb: 'gå'},
        {az: 'dayan', nb: 'stopp'},
        {az: 'vurma', nb: 'ikke slå'},
        {az: 'itələmə', nb: 'ikke dytt'},
        {az: 'Olmaz', nb: '"slik gjør man ikke"'},
        {az: 'Nə qəşəng', nb: 'så fin'},
        {az: 'afərin', nb: 'kjempebra'},
        {az: 'əla', nb: 'supert'},
        {az: 'bir az gözlə', nb: 'vent litt'},
        {az: 'Bax', nb: 'se!'},
        {az: '(məni) burax', nb: 'slipp (meg)'},
        {az: 'götür', nb: 'plukk opp'},
        {az: 'apar', nb: 'ta med'},
        {az: 'o batırıb', nb: 'han/hun har bæsja'},
        {az: 'Kim qalıb?', nb: 'Hvem gjenstår?'},
        {az: 'Kim yeyib?', nb: 'Hvem har spist?'},
        {az: 'O yeyib', nb: 'Han/hun har spist'},
        {az: 'o yeməyib', nb: 'Han/hun har ikke spist'},
        {az: 'vsyo', nb: 'Ferdig/det var det/det var alt'},
        {az: '(...) istəyirsən?', nb: 'Vil du (ha ...)?'},
        {az: 'Stul', nb: 'stol'},
        {az: 'Dur', nb: 'stå'},
        {az: 'otur', nb: 'sitt'},
        {az: 'həyətə', nb: 'ut (om å gå ut etc) (egentlig "til gårdsplassen")'}
      ]
    }
  },
  methods: {
    install () {
      // hide our user interface that shows our A2HS button
      this.allowInstall = false
      // Show the prompt
      window.deferredPrompt.prompt()
      // Wait for the user to respond to the prompt
      window.deferredPrompt.userChoice
        .then((choiceResult) => {
          if (choiceResult.outcome === 'accepted') {
            console.log('User accepted the A2HS prompt')
          } else {
            console.log('User dismissed the A2HS prompt')
          }
          window.deferredPrompt = null
        })
    }
  },
  mounted () {
    console.log('Installing event handler')
    window.addEventListener('beforeinstallprompt', (e) => {
      console.log('Got beforeinstallprompt event!')
      // Prevent Chrome 67 and earlier from automatically showing the prompt
      e.preventDefault()
      // Stash the event so it can be triggered later.
      window.deferredPrompt = e
      // Update UI notify the user they can add to home screen
      this.allowInstall = true
    })
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.header {
  text-align: center;
}

img {
  max-width: 300px;
}

ons-list-title {
  text-transform: none;
}

ons-list-title:not(:first-of-type) {
  margin-top: 30px;
}

ons-card {
  text-align: center;
}

ons-list-item, ons-card {
  cursor: pointer;
}
</style>



